export class TableToolsProvider {
    constructor() {
        this.perPage = 25;
        this.perPageOptions = [
            { number: 10, text: '10' },
            { number: 25, text: '25' },
            { number: 50, text: '50' },
            { number: 100, text: '100' },
            { number: 200, text: '200' },
            { number: Infinity, text: 'Wszystkie' }
        ];
        this.scrollOffset = 0;
        this.lang = {
            first: 'Pierwsza strona',
            prev: 'Poprzednia strona',
            next: 'Nastpna strona',
            last: 'Ostatnia strona',
            results: 'Wyniki:',
            from: 'z',
            perPage: 'Wynikw na stron:',
            search: 'Szukaj...',
            filteredResults: 'Filtrowanie z:',
            export: 'Export',
            exportChooseColumns: 'Wybierz kolumny',
            flipSelection: 'odwr zaznaczenie',
            exportColumnNames: 'Eksportuj nazwy kolumn',
            exportSeparator: 'Separator',
            tabulator: 'Tabulator',
            copy: 'Kopiuj',
            csv: 'CSV',
            copiedToClipboard: 'Skopiowano do schowka',
            noResults: 'Nie znaleziono adnych wynikw!'
        };
        this.exportTypes = {
            copy: {
                lang: this.lang.copy
            },
            csv: {
                lang: this.lang.csv,
                parseText(txt) {
                    return '"' + txt.replace('"', '""') + '"';
                }
            }
        };
    }
    $get() {
        return this;
    }
    exportNotification(type) {
        if (type === 'copy') {
            // eslint-disable-next-line no-alert
            alert(this.lang.copiedToClipboard);
        }
    }
}
